.c-dooca__navExtraMenu {
	display: block;
	width: 100%;
	ul {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: flex-end;
		li {
			margin-right: 20px;
			&:last-of-type {
				margin-right: 0;
			}
		}
		a {
			@include smallUppercase;
			white-space: normal;
			i {
				color: $white;
				span {
					position: relative;
					top: 2px;
				}
			}
			&:hover {
				color: $primary;
				* {
					color: $primary;
				}
			}
		}
	}
}
.c-dooca__navSocialList {
	display: block;
	width: 100%;
	ul {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: flex-end;
		li {
			margin-right: 20px;
			&:last-of-type {
				margin-right: 0;
			}
		}
		a {
			@include smallUppercase;
			white-space: normal;
			&:hover {
				color: $primary;
			}
		}
	}
}
.c-dooca__navCategoriesMenu {
	ul.navCategoriesMenu__hero {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: flex-end;
		> li.navigation-item {
			margin-right: 25px;
			position: relative;
			&:last-of-type {
				margin-right: 0;
			}

			// itens principais
			> a.navigation-link {
				display: block;
				span {
					@include letterSpacing(40);
					font-family: $fontFamily__leagueGothic;
					font-weight: $fwRegular;
					font-size: 24px;
					color: $primary;
					height: 50px;
					line-height: 50px;
					text-transform: uppercase;
				}
				&:hover {
					span {
						color: $white;
					}
				}
			}

			// sub menu
			.menu-dropdown {
				@include transition();
				@include u-hideElement;
				@include translate(0, -50%, center);
				display: block;
				width: 300px;
				position: absolute;
				left: 50%;
				top: 45px;
				background: $primary;
				padding: 45px;
				&:before {
					content: '';
					display: block;
					width: 0;
					height: 0;
					border-style: solid;
					border-width: 0 8px 8px 8px;
					border-color: transparent transparent $primary transparent;
					position: absolute;
					left: 50%;
					top: -8px;
					margin-left: -8px;
				}
				.menu-dropdown-container {
					> div {

					}
					.menu-dropdown-item {
						margin-bottom: 10px;
						&:last-of-type {
							margin-bottom: 0;
						}
						.menu-dropdown-link {
							display: block;
							padding: 0 !important;
							span {
								@include letterSpacing(30);
								font-weight: $fwRegular;
								font-size: 24px;
								color: $black;
								text-transform: uppercase;
								font-family: $fontFamily__leagueGothic;
							}
							&:hover {
								span {
									text-decoration: underline;
								}
							}
						}
					}
				}
			}

			&:hover {
				.menu-dropdown {
					@include u-showElement;
					top: 50px;
				}
			}
		}
	}
}
.c-dooca__navSearch {
