.l-nav {
	@include transition();
	display: block;
	width: 100%;
	max-width: 100vw;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 10;

	&__main {
		@include transition();
		display: block;
		padding: 50px 0;
	}
	&__brand {
		@include transition();
		@include flexWidth(150px);
		display: block;
		* {
			@include transition();
			display: block;
			width: 100%;
		}
	}
	&__contentHero {
		flex: 1 1 auto;
		display: block;
		padding-left: 45px;
		.contentHero__top,
		.contentHero__bottom {
			@include transition();
			width: 100%;
			display: flex;
			flex-direction: row;
			align-items: center;
			justify-content: flex-end;
			flex-wrap: nowrap;
		}
		.contentHero__top {
			margin-bottom: 30px;
			padding-bottom: 30px;
			border-bottom: 1px solid rgba(255,255,255,0.2);
			overflow: hidden;
			max-height: 100px;
		}
	}
	&__contact {
		margin-right: 30px;
		padding-right: 30px;
		border-right: 1px solid rgba(255,255,255,0.2);
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: flex-start;
		flex: 1 1 auto;
	}
	&__menu {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		position: relative;
		margin-right: 30px;
		padding-right: 30px;
		border-right: 1px solid rgba(255,255,255,0.2);
		z-index: 2;
		> li {
			display: block;
			position: relative;
			margin-right: 25px;
			&:last-of-type {
				margin-right: 0;
			}
		}
	}
	&__social {
		display: block;
	}
	&__controls {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: flex-end;
		> div {
			margin-right: 10px;
			&:last-of-type {
				margin-right: 0;
			}
		}
	}

	&__hamburguer {
		display: none;
	}

	&.is-middle {

	}
	&.is-scrolled {
		background: $secondary;
		.l-nav__main {
			padding: 10px 0;
		}
		.l-nav__brand {
			@include flexWidth(70px);
		}
		.l-nav__contentHero {
