	&__content {
		@include transition($delay: 50ms, $duration: 280ms);
		@include scroll($bg: $secondaryLight, $bar: $primary);
		display: block;
		width: 100%;
		max-width: 290px;
		height: 100vh;
		background: $secondary;
		position: fixed;
		top: 0;
		z-index: 2;
		overflow-x: hidden;
		overflow-y: auto;
		padding: 30px 30px 90px 30px;
		&.--left {
			left: -500px;
		}
		&.--right {
			right: -500px;
		}
	}
	&.is-active {
		@include u-showElement;
		.l-navHidden__content {
			@include opacity(1);
			&.--left {
				left: 0;
			}
			&.--right {
				right: 0;
			}
		}
	}

	.u-spacer {
		@include opacity(0.2);
		margin: 35px 0;
