// l-nav / l-navHidden
@media screen and (max-width: 880px) {
    .l-nav {
        &__main {
            padding: 30px 0;
        }
        &__brand {
            @include flexWidth(110px);
        }
        &__contentHero {
            .contentHero__top {
                margin-bottom: 15px;
                padding-bottom: 15px;
            }
        }
        &__menu {
            margin-right: 15px;
            padding-right: 15px;
        }
        &__contact {
            margin-right: 15px;
            padding-right: 15px;
        }
    }
}
@media screen and (max-width: 800px) {
    .l-nav {
        &__contentHero {
            padding-left: 0;
            .contentHero__top {
                display: none;
            }
        }
        &__menu {
            display: none;
        }
        &__hamburguer {
            @include flexWidth(50px);
            display: block;
            margin-left: 20px;
            padding-left: 20px;
            border-left: 1px solid rgba(255,255,255,0.2);
        }
    }
    .l-navHidden {
        display: block;
    }
}
@media screen and (max-width: 420px) {
    .l-nav {
        &__brand {
            @include flexWidth(80px);
        }
    }
}
@media screen and (max-width: 350px) {
    .l-nav {
        &__main {
            padding: 15px 0;
        }
        &__brand {
            @include flexWidth(70px);
        }
        &__hamburguer {
            @include flexWidth(35px);
            display: block;
            margin-left: 10px;
            padding-left: 5px;
            border-left: 0;
        }
    }
}

// l header home
@media screen and (max-width: 1170px) {
    .c-dooca__sliderItem__figure figure img,
    .c-dooca__sliderItem__figure figure a img {
        height: 660px;
        max-height: inherit;
    }
}
@media screen and (max-width: 1080px) {
    .c-dooca__sliderItem__content {
        @include flexWidth(400px);
        h2 {
            font-size: 80px;
        }
        h3 {
            font-size: 18px;
        }
    }
}
@media screen and (max-width: 930px) {
    .c-dooca__sliderItem__content {
        padding-top: 0;
    }
    .c-dooca__sliderItem__figure figure img,
    .c-dooca__sliderItem__figure figure a img {
        height: 580px;
    }
}
@media screen and (max-width: 850px) {
    .c-dooca__sliderItem__figure figure img,
    .c-dooca__sliderItem__figure figure a img {
        height: 530px;
    }
}
@media screen and (max-width: 780px) {
    section.section.section--banners-carousel {
        display: none;
    }
    section.section.section--banners-grid {
        display: block;
        padding-top: 150px;
        padding-bottom: 80px;
        .container {
            max-width: 100%;
        }
    }

    .banners-grid-content {
        width: 100%;
        > .col-12 {
            display: none !important;
            width: 100% !important;
            max-width: 100% !important;
            flex: 1 1 auto !important;
            &:last-of-type {
                display: block !important;
            }
            * {
                display: block !important;
                width: 100% !important;
                flex: 1 1 auto !important;
                max-width: 100% !important;
            }
        }
    }
}

// header home - controls
@media screen and (max-width: 1500px) {
    section.section.section--banners-carousel .owl-dots {
        left: 5px;
    }
    section.section.section--banners-carousel .owl-nav {
        right: 10px;
    }
}
@media screen and (max-width: 1320px) {
    section.section.section--banners-carousel .owl-nav {
        display: none;
    }
    section.section.section--banners-carousel .owl-dots {
        top: inherit;
        bottom: 150px !important;
        width: 100%;
        text-align: left;
    }
    section.section.section--banners-carousel .owl-dots .owl-dot {
        margin-bottom: 0 !important;
        display: inline-block;
        margin-right: 10px !important;
    }
}

// footer
@media screen and (max-width: 1040px) {
    .l-footer__col {
        padding: 0;
    }
    .l-footer__right {
        left: 0;
        width: 100%;
        padding-left: 30px;
    }
}
@media screen and (max-width: 850px) {
    .l-footer {
        .l-footer__brand {
            max-width: 240px;
            margin: 0 auto;
        }
        .c-dooca__footerTickets {
            .container {
                max-width: 100%;
            }
        }
        .u-container {
            flex-wrap: wrap;
        }
    }
    .l-footer__right,
    .l-footer__left {
        @include flexWidth(100%);
    }

    .l-footer__right {
        padding-left: 0;
    }
}
@media screen and (max-width: 768px) {
    .l-footer__col .h2 {
        text-align: center;
    }
    .footer-payment {
        margin-bottom: 60px;
        .no-gutters {
            max-width: 100%;
            justify-content: center;
        }
    }
    .footer-security {
        .row {
            justify-content: center;
        }
    }
}
@media screen and (max-width: 590px) {
    .l-footer .l-footer__brand {
        top: 0;
        margin-bottom: 60px;
    }
    .footer-navigation {
        .row {
            flex-wrap: wrap;
            .col {
                @include flexWidth(100%);
                margin-bottom: 60px;
                &:last-of-type {
                    margin-bottom: 0;
                }
            }
        }
    }

    .l-footerCopyright__content {
        flex-wrap: wrap;
        justify-content: center;
        * {
            @include flexWidth(100%);
            text-align: center;
        }
    }
}

// grid de selos da loja
@media screen and (max-width: 1180px) {
    section.section.section--banners-commercial .c-cardSeloLoja>div,
    section.section.section--banners-commercial .c-cardSeloLoja>a {
        justify-content: center;
        flex-wrap: wrap;
    }
    section.section.section--banners-commercial .c-cardSeloLoja>div img,
    section.section.section--banners-commercial .c-cardSeloLoja>a img {
        margin-bottom: 20px;
    }
    section.section.section--banners-commercial .c-cardSeloLoja>div>div,
    section.section.section--banners-commercial .c-cardSeloLoja>a>div {
        @include flexWidth(100%);
        padding-left: 0;
        * {
            text-align: center;
        }
    }
}
@media screen and (max-width: 860px) {
    section.section.section--banners-commercial .c-cardSeloLoja {
        @include flexWidth(50%);
        margin-bottom: 15px;
        &:last-of-type {
            margin-bottom: 0;
        }
    }
}
@media screen and (max-width: 780px) {
    section.section.section--banners-commercial {
        padding: 80px 0;
        margin-top: 0;
    }
}
@media screen and (max-width: 560px) {
    section.section.section--banners-commercial .c-cardSeloLoja {
        @include flexWidth(100%);
        margin-bottom: 30px;
        &:last-of-type {
            margin-bottom: 0;
        }
    }
}
@media screen and (max-width: 460px) {
    section.section.section--banners-grid {
        padding-bottom: 0;
    }
    section.section.section--banners-grid .container {
        padding: 0 !important;
        width: 100% !important;
        left: 0 !important;
        .banners-grid-main,
        .banners-grid-block {
            padding: 0px !important;
        }
        .banners-grid-blocks {
            > div {
                margin-top: 4px;
            }
        }
    }
}

// produto destaque
@media screen and (max-width: 1440px) {
    .s-featuredProduct__image {
        @include flexWidth(650px);
        .banner-bg {
            background-position: top right;
        }
    }
    .s-featuredProduct__content {
        .content__hero {
            padding: 0 20px;
            max-width: 100%;
        }
    }
}
@media screen and (max-width: 1080px) {
    .s-featuredProduct__content {
        .content__hero {
            h3 {
                font-size: 80px;
            }
            .title {
                .section-title {
                    h2 {
                        font-size: 22px;
                    }
                }
            }
            .description {
                font-size: 18px;
            }
        }
    }
}
@media screen and (max-width: 990px) {
    .s-featuredProduct__image {
        @include flexWidth(500px);
        img {
            height: 780px;
        }
        .banner-bg {
            background-position: top right;
        }
    }
}
@media screen and (max-width: 1080px) {
    .s-featuredProduct__content {
        .content__hero {
            padding-right: 15px;
            padding-left: 15px;
        }
    }
}
@media screen and (max-width: 780px) {
    section.section--image-text {
        display: none;
    }
}

// compre por categoria
@media screen and (max-width: 992px) {
    section.section.section--banners .banners {
        padding-bottom: 100px;
        .col-6 {
            margin-bottom: 55px;
        }
    }
}

// newsletter
@media screen and (max-width: 650px) {
    .s-newsletter {
        .c-form__fieldsHero.--pad5>.c-form__field {
            @include flexWidth(100%);
        }
    }
}

// destaques do mes
@media screen and (max-width: 992px) {
    section.section.section--products-tabs {
        z-index: 1;
        position: relative;
    }
    .products-tabs-select {
        display: block !important;
        .products-tabs-icon {
            display: block !important;
        }
        select {
            display: block !important;
            color: $black !important;
        }
    }
    .products-tabs-item {
        display: none !important;
    }
    .products-tabs-slider.d-lg-none {
        display: none !important;
    }
    .products-tabs-slider.d-lg-flex {
        display: flex !important;
        > div.col-12 {
            @include flexWidth(50%);
        }
    }
}
@media screen and (max-width: 780px) {
    section.section.section--products-tabs {
        background: $white;
        padding-top: 60px;
    }
}
@media screen and (max-width: 415px) {
    .products-tabs-slider.d-lg-flex {
        > div.col-12 {
            @include flexWidth(100%);
        }
    }
}

// padding top content inner pages
@media screen and (max-width: 880px) {
    body.page--collection .wrapper,
    body.page--product .wrapper {
        padding-top: 180px;
    }
}
@media screen and (max-width: 800px) {
    body.page--collection .wrapper,
    body.page--product .wrapper {
        padding-top: 160px;
    }
}
@media screen and (max-width: 420px) {
    body.page--collection .wrapper,
    body.page--product .wrapper {
        padding-top: 135px;
    }
}


// page product - veja tbm
@media screen and (max-width: 992px) {
    #product-related {
        .col-md-3 {
            @include flexWidth(50%);
        }
    }
}
@media screen and (max-width: 415px) {
    #product-related {
        .col-md-3 {
            @include flexWidth(100%);
        }
    }
}