// boilerplate
$componentBaseColor: $lightGray;
$textColor: $text;
.c-component {
    @include roundedAll();
    border: 1px solid darken($componentBaseColor, 5%);
    background: $componentBaseColor;
    overflow: hidden;
    display: block;
    
    &__head {
        background: $componentBaseColor;
        padding: 15px;
        min-height: 70px;
    }
    .head__title {
        @include labels($tc: $textColor);
    }
    .head__options {
        > span, a {
            @include transition();
            @include roundedAll();
            width: 32px;
            height: 32px;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            background-color: transparent;
            cursor: pointer;
            &:hover {
                background: darken($componentBaseColor, 5%);
            }
            svg, img, i {
                width: 20px;
                height: 20px;
                display: block;
                object-fit: contain;
            }
            svg {
                path {
                    fill: $textColor;
                }
            }
        }
    }
    
    &__inputHidden {
        position: absolute;
        top: 0;
        left: 0;
        width: 1px;
        height: 1px;
        @include opacity(0);
    }
    
    &__content {
        @include scroll($bar: $mediumGray, $bg: $white2, $size: 2px);
        @include transition();
        min-height: 80px;
        display: block;
        position: relative;
        padding: 20px;
        background: $white3;
        overflow: auto;
    }
    
    &__hero {
        position: relative;
        z-index: 1;
    }
    
    &__observations {
        @include scroll($bar: $mediumGray, $bg: $white, $size: 4px);
        overflow: auto;
        display: none;
        width: 100%;
        height: 100%;
        padding: 20px;
        position: absolute;
        top: 0;
        right: 0;
        z-index: 2;
        background: rgba(255,255,255,0.95);
        p {
            @include paragrafos($fs: 16px);
        }
    }
    
    &.is-obs-active {
        .c-component__content {
            min-height: 120px;
        }
        .c-component__observations {
            display: block;
        }
    }
}

// form / alerts
$field_height: 60px;
$field_padding: 20px;
$field_radius: $defaultRadius;
$fields_borderColor: transparent;
$fields_borderColor_focus: transparent;
$field_bg: rgba(0,0,0,0.5);
$field_bg_focus: $secondaryDark;
$spacer_bg: $secondaryDark;
$field_checks_size: 20px;
.c-form {
    &__fieldsHero {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        &.--pad5 {
            @include calcWidthForPositionNegative(-5px);
            > .c-form__field {
                padding: 5px;
                &.--file {
                    .inputAttach {
                        @include calc(width, 100%, '-', 10px);
                        left: 5px;
                        bottom: -20px;
                    }
                    &:after {
                        bottom: 17px;
                        right: 25px;
                    }
                }
            }
            > .c-form__spacer {
                @include calc(width, 100%, '-', 10px);
                left: 5px;
                margin: 5px 0;
            }
        }
        &.--pad10 {
            @include calcWidthForPositionNegative(-10px);
            > .c-form__field {
                padding: 10px;
                &.--file {
                    .inputAttach {
                        @include calc(width, 100%, '-', 20px);
                        left: 10px;
                        bottom: -15px;
                    }
                    &:after {
                        bottom: 22px;
                        right: 30px;
                    }
                }
            }
            > .c-form__spacer {
                @include calc(width, 100%, '-', 20px);
                left: 10px;
                margin: 10px 0;
            }
        }
        &.--pad15 {
            @include calcWidthForPositionNegative(-15px);
            > .c-form__field {
                padding: 15px;
                &.--file {
                    .inputAttach {
                        @include calc(width, 100%, '-', 30px);
                        left: 15px;
                        bottom: -10px;
                    }
                    &:after {
                        bottom: 27px;
                        right: 35px;
                    }
                }
            }
            > .c-form__spacer {
                @include calc(width, 100%, '-', 30px);
                left: 15px;
                margin: 15px 0;
            }
        }
        &.--pad20 {
            @include calcWidthForPositionNegative(-20px);
            > .c-form__field {
                padding: 20px;
                &.--file {
                    .inputAttach {
                        @include calc(width, 100%, '-', 40px);
                        left: 20px;
                        bottom: -5px;
                    }
                    &:after {
                        bottom: 32px;
                        right: 40px;
                    }
                }
            }
            > .c-form__spacer {
                @include calc(width, 100%, '-', 40px);
                left: 20px;
                margin: 20px 0;
            }
        }
        &.--pad25 {
            @include calcWidthForPositionNegative(-25px);
            > .c-form__field {
                padding: 25px;
                &.--file {
                    .inputAttach {
                        @include calc(width, 100%, '-', 50px);
                        left: 25px;
                        bottom: 0;
                    }
                    &:after {
                        bottom: 35px;
                        right: 45px;
                    }
                }
            }
            > .c-form__spacer {
                @include calc(width, 100%, '-', 50px);
                left: 25px;
                margin: 25px 0;
            }
        }
    }
    
    // spacer
    &__spacer {
        display: block;
        position: relative;
        height: 1px;
        background: $spacer_bg;
    }
    
    // all elements
    input, textarea, select, label, .fakeLabel {
        display: block;
        width: 100%;
    }
    
    // fields
    input, select, textarea {
        @include transition();
        @include roundedAll($field_radius);
        @include paragrafos();
        background: $field_bg;
        border: 1px solid $fields_borderColor;
        font-size: 32px;
        font-family: $fontFamily__leagueGothic;
        font-weight: $fwRegular;
        @include placeholder {
            @include labels($tc: $placeholderText);
            font-size: 32px;
            font-family: $fontFamily__leagueGothic;
            font-weight: $fwRegular;
            text-transform: uppercase;
        }
        &:focus {
            border-color: $fields_borderColor_focus;
            background: $field_bg_focus;
        }
    }
    input, select {
        height: $field_height;
        padding: 0 $field_padding;
    }
    textarea {
        padding: $field_padding;
        min-height: $field_height * 2;
        max-height: 300px;
        resize: vertical;
    }
    
    // titles & labes
    label, .fakeLabel {
        @include labels();
        margin-bottom: 4px;
        &.--withObs {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            .caption, strong {
                @include labels();
            }
            .obs, .observation {
                @include labels();
                font-weight: $fwLight;
            }
        }
    }
    
    // fields with modifiers
    &__field {
        position: relative;
        &.--range {
            .rangeHero {
                @include calcWidthForPositionNegative(-5px);
                display: flex;
                flex-direction: row;
            }
            .min, .max {
                padding: 0 5px;
                @include flexWidth(50%);
            }
        }
        &.--check {
            .checkHero {
                > * {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    margin-bottom: 10px;
                    &:last-of-type {
                        margin-bottom: 0;
                    }
                    &:first-of-type {
                        margin-top: 5px;
                    }
                }
            }
            .fakeLabel {
                margin-bottom: 8px;
            }
            label {
                @include paragrafos($tc: 16px);
                padding-left: 5px;
                cursor: pointer;
                order: 2;
            }
            input {
                @include flexWidth($field_checks_size);
                height: $field_checks_size;
                padding: 0;
                border: 0;
                order: 1;
                &:checked {
                    background: url(#{$media_icons}/icon_checked.svg);
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-position: center;
                }
            }
            &.--checkbox {
                input {
                    @include roundedAll(4px);
                }
            }
            &.--radio {
                input {
                    @include roundedAll(50%);
                }
            }
        }
        &.--file {
            position: relative;
            .inputText {
                position: relative;
                z-index: 1;
                cursor: pointer;
                padding-right: 50px;
            }
            .inputAttach {
                //background: red;
                cursor: pointer;
                background: transparent;
                position: absolute;
                z-index: 2;
                width: 100%;
                @include opacity(0);
            }
            
            &:after {
                @include transforms(rotate(0deg), center);
                @include transition();
                content: '';
                width: 20px;
                height: 20px;
                display: block;
                position: absolute;
                bottom: 22px;
                right: 35px;
                background: url(#{$media_icons}icon_attach.svg);
                background-repeat: no-repeat;
                background-position: center center;
                background-size: contain;
                z-index: 1;
            }
        }
        &.--select {
            position: relative;
            * {
                @include transition(all, 0ms, linear);
            }
            .selectBox,
            .inputBox {
                @include roundedAll($field_radius);
                background: $field_bg;
                height: $field_height;
                display: block;
                position: relative;
                width: 100%;
            }
            .selectBox {
                select {
                    @include opacity(0);
                    display: block;
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    z-index: 2;
                }
            }
            .inputBox {
                z-index: 1;
                &:after {
                    @include transforms(rotate(0deg) translateY(50%), center);
                    @include transition();
                    content: '';
                    width: 12px;
                    height: 10px;
                    display: block;
                    position: absolute;
                    bottom: 50%;
                    right: 20px;
                    background: url(#{$media_assets}caret-down.svg);
                    background-repeat: no-repeat;
                    background-position: center center;
                    background-size: contain;
                }
            }
            
            &.is-active {
                .selectBox {
                    select {
                        @include opacity(1);
                        background: transparent;
                        height: $field_height;
                    }
                }
                .inputBox {
                    input {
                        @include opacity(0);
                    }
                    &:after {
                        @include transforms(rotate(180deg) translateY(50%), bottom);
                    }
                }
            }
        }
        &.--select.--full {
            .selectBox {
                height: auto;
                background-color: transparent;
            }
        }
        &.--select2 {
            .select2 {
                width: 100% !important;
                position: relative;
                .selection {
                    .select2-selection {
                        &:after {
                            @include transition();
                            content: '';
                            width: 12px;
                            height: 10px;
                            display: block;
                            position: absolute;
                            bottom: ($field_height / 2) - 5px;
                            right: 20px;
                            z-index: 4;
                            background: url(#{$media_assets}caret-down.svg);
                            background-repeat: no-repeat;
                            background-position: center center;
                            background-size: contain;
                        }
                    }
                }
                &.select2-container--open {
                    .selection {
                        .select2-selection {
                            &:after {
                                @include transforms(rotate(180deg), center);
                            }
                        }
                    }
                }
            }
        }
        &.--select2.--default {
            .select2-container--default {
                .select2-selection--single {
                    @include roundedAll(0);
                    border: 0;
                    background: transparent;
                    height: $field_height;
                    .select2-selection__rendered {
                        @include paragrafos();
                        @include roundedAll($field_radius);
                        padding: 0 $field_radius;
                        border-color: $fields_borderColor;
                        background: $field_bg;
                        height: $field_height;
                        line-height: $field_height;
                    }
                    .select2-selection__placeholder {
                        @include labels($tc: $placeholderText);
                        height: $field_height;
                        line-height: $field_height;
                    }
                    .select2-selection__arrow {
                        display: none;
                    }
                }
            }
        }
        &.--select2.--multiple {
            .select2-container--default {
                .selection {
                    .select2-selection {
                        .select2-search {
                            @include roundedAll($field_radius);
                            height: $field_height;
                            display: block;
                            position: relative;
                            background: $field_bg_focus;
                            &:after {
                                content: '';
                                display: block;
                                width: 20px;
                                height: 20px;
                                position: absolute;
                                top: 12px;
                                left: 20px;
                                z-index: 2;
                                background: url(#{$media_icons}icon_search.svg);
                            }
                            .select2-search__field {
                                @include transition($property: background);
                                background: $field_bg;
                                margin-top: 0;
                                margin-left: 0;
                                padding: 0 $field_padding;
                                border: 1px solid $fields_borderColor;
                                position: relative;
                                z-index: 3;
                                &:focus {
                                    border: 1px solid $fields_borderColor_focus;
                                    background-color: transparent;
                                    padding-left: 50px;
                                    z-index: 1;
                                }
                            }
                        }
                    }
                }
                .select2-selection--multiple {
                    @include roundedAll(0);
                    background-color: transparent;
                    border: 0;
                    padding-bottom: 0;
                    padding-right: 0;
                }
            }
        }
        &.--btn {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: flex-end;
        }
        &.--text {
            p {
                @include paragrafos();
            }
        }
        &.--rangeDefault {
            label {
                margin-bottom: 10px;
            }
            .rangeSlider {
                display: block;
                position: relative;
                width: 100%;
                padding-bottom: 5px;
            }
            
            .rangeSlider__range {
                width: calc(100% - (55px));
                height: 4px;
                border-radius: 2px;
                background: $field_bg_focus;
                outline: none;
                padding: 0;
                margin: 0;
            }
            .rangeSlider__range::-webkit-slider-thumb {
                -webkit-appearance: none;
                appearance: none;
                width: 10px;
                height: 10px;
                border-radius: 50%;
                background: $primary;
                cursor: pointer;
                -webkit-transition: background .15s ease-in-out;
                transition: background .15s ease-in-out;
            }
            .rangeSlider__range::-webkit-slider-thumb:hover {
                background: $primaryDark;
            }
            .rangeSlider__range:active::-webkit-slider-thumb {
                background: #393939;
            }
            .rangeSlider__range::-moz-range-thumb {
                width: 10px;
                height: 10px;
                border: 0;
                border-radius: 50%;
                background: $primary;
                cursor: pointer;
                -moz-transition: background .15s ease-in-out;
                transition: background .15s ease-in-out;
            }
            .rangeSlider__range::-moz-range-thumb:hover {
                background: $primaryDark;
            }
            .rangeSlider__range:active::-moz-range-thumb {
                background: #393939;
            }
            .rangeSlider__range:focus::-webkit-slider-thumb {
                //box-shadow: 0 0 0 3px #fff, 0 0 0 6px #1abc9c;
            }
            
            .rangeSlider__value {
                @include roundedAll(10px);
                @include labels($tc: $white, $fs: 14px);
                display: block;
                width: 40px;
                line-height: 20px;
                height: 20px;
                text-align: center;
                background: $primary;
                position: absolute;
                right: 0;
                top: 50%;
                margin-top: -10px;
            }
            .rangeSlider__value:after {
                position: absolute;
                top: 3px;
                left: -5px;
                width: 0;
                height: 0;
                border-top: 7px solid transparent;
                border-right: 7px solid $primary;
                border-bottom: 7px solid transparent;
                content: '';
            }
            
            ::-moz-range-track {
                background: #393939;
                border: 0;
            }
            
            input::-moz-focus-inner,
            input::-moz-focus-outer {
                border: 0;
            }
        }
        &.--rangeDefault.--withArrow {
            padding-left: 50px;
            &:before {
                content: '';
                display: block;
                width: 20px;
                height: 20px;
                position: absolute;
                top: 10px;
                left: 20px;
                background: red;
                background: url(#{$media_icons}icon_sub.png);
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
            }
        }
    }
    
    // form with steps
    &__step {
        margin-top: 50px;
        h2 {
            margin-bottom: 10px;
        }
        &:first-of-type {
            margin-top: 0;
        }
    }
}
.c-formFeedback {
    @keyframes animationRotateLoader {
        from {@include transforms(rotate(0deg), center)}
        to {@include transforms(rotate(360deg), center)}
    }
    @include roundedAll($radius: 5px);
    position: relative;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    width: 100%;
    line-height: 28px;
    padding: 20px;
    i {
        @include flexWidth(40px);
        max-height: 40px;
        svg {
            width: 40px;
            height: 40px;
        }
    }
    span {
        @include letterSpacing($psValue: 50);
        display: block;
        padding-left: 15px;
        font-size: 16px;
        line-height: 1.2;
    }
    
    &.--error {
        background: $feedbackError;
        span {
            color: $white;
        }
    }
    &.--load {
        background: $feedbackLoad;
        span {
            color: $text;
        }
        i {
            animation-name: animationRotateLoader;
            animation-duration: 1s;
            animation-iteration-count: infinite;
            animation-timing-function: linear;
        }
    }
    &.--success {
        background: $feedbackSuccess ;
        span {
            color: $white;
        }
    }
}

// btns
$btn_maxWidth: 220px;
$btn_height: 60px;
$btn_heightSmall: 44px;
$btn_radius: 6px;
$btn_iconSize: 28px;
$btn_text_fontSize: 28px;
$btn_text_fontWeight: $fwRegular;
.c-btn {
    @include transition();
    @include roundedAll($btn_radius);
    display: block;
    width: 100%;
    max-width: $btn_maxWidth;
    height: $btn_height;
    line-height: $btn_height;
    font-size: $btn_text_fontSize;
    font-weight: $btn_text_fontWeight;
    font-family: $fontFamily__leagueGothic;
    text-transform: uppercase;
    text-align: center;
    cursor: pointer;
    
    // colors modifiers
    &.--primary {
        background: $primary;
        color: $black;
        &:hover {
            background: $primaryDark;
        }
    }
    &.--secondary {
        background: $secondary;
        color: $primary;
        &:hover {
            background: $secondaryDark;
        }
    }
    &.--white {
        background: $white;
        color: $text;
        &:hover {
            background: $white2;
        }
    }
    &.--disabled {
        pointer-events: none;
        background: $mediumGray;
        color: $baseGray;
    }
    
    // size modifiers
    &.--small {
        height: $btn_heightSmall;
        line-height: $btn_heightSmall;
    }
    
    // stroke
    &.--stroke {
        background: transparent;
        border: 2px solid;
        line-height: $btn_height - 4px;
        &.--primary {
            border-color: $primary;
            color: $primary;
            &:hover {
                border-color: $primaryDark;
                color: $white;
            }
        }
        &.--secondary {
            border-color: $secondary;
            color: $secondary;
            &:hover {
                border-color: $secondaryDark;
                color: $white;
            }
        }
        &.--white {
            border-color: $white;
            color: $white;
            &:hover {
                color: $baseGray;
            }
        }
        &.--disabled {
            border-color: $mediumGray;
            color: $baseGray;
        }
    }
    
    // with icon
    &.--icon {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        flex-wrap: nowrap;
        i {
            display: block;
            width: $btn_iconSize;
            height: $btn_iconSize;
            svg, img {
                display: block;
                width: $btn_iconSize;
                height: $btn_iconSize;
                object-fit: contain;
                object-position: center;
                path {
                    @include transition();
                }
            }
        }
        span {
            padding-left: 8px;
            font-size: $btn_text_fontSize;
            font-weight: $btn_text_fontWeight;
        }
        &.--primary {
            svg path {
                fill: $white;
            }
        }
        &.--secondary {
            svg path {
                fill: $white;
            }
        }
        &.--disabled {
            svg path {
                fill: $baseGray;
            }
        }
        &.--white {
            svg path {
                fill: $text;
            }
        }
    }
    
    // stroke with icon
    &.--icon.--stroke {
        &.--primary {
            svg path {
                fill: $primary;
            }
            &:hover {
                svg path {
                    fill: $white;
                }
            }
        }
        &.--secondary {
            svg path {
                fill: $secondary;
            }
            &:hover {
                svg path {
                    fill: $white;
                }
            }
        }
        &.--white {
            svg path {
                fill: $white;
            }
            &:hover {
                svg path {
                    fill: $baseGray;
                }
            }
        }
        &.--disabled {
            svg path {
                fill: $mediumGray;
            }
        }
    }
    
    // padding adjust
    &.--withPadding {
        padding: 0 20px;
    }
}

// menus e sub-menus
.c-hamburguer {
    @include flexWidth(30px);
    @include transition();
    height: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
    &__bar {
        @include transition();
        display: block;
        width: 100%;
        height: 2px;
        position: relative;
    }
    
    &.--gray {
        .c-hamburguer__bar {
            background: $baseGray;
        }
    }
    &.--white {
        .c-hamburguer__bar {
            background: $white;
        }
    }
    &.--primary {
        .c-hamburguer__bar {
            background: $primary;
        }
    }
    &.--secondary {
        .c-hamburguer__bar {
            background: $secondary;
        }
    }
    
    &.is-active {
        @include transforms(rotate(180deg), center);
        .c-hamburguer__bar {
            &:nth-child(1) {
                @include transforms(rotate(45deg), center);
                top: 7px;
            }
            &:nth-child(2) {
                @include opacity(0);
            }
            &:nth-child(3) {
                @include transforms(rotate(-45deg), center);
                top: -8px;
            }
        }
    }
}
.c-subMenu {
    @include transition();
    display: block;
    position: relative;
    z-index: 4;
    // default styles
    > .c-subMenu__call {
        @include transition();
        display: block;
        width: 100%;
        position: relative;
        text-align: center;
        cursor: pointer;
        z-index: 2;
        &:after {
            @include transition();
            content: '';
            display: block;
            width: 12px;
            height: 6px;
            position: absolute;
            bottom: -12px;
            left: 0;
            right: 0;
            margin: 0 auto;
            background: url(#{$media_assets}caret-down.svg);
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
        }
        &.--btn {
            &:after {
            
            }
        }
    }
    > .c-subMenu__content {
        @include transition();
        @include scroll();
        @include calc(max-height, 100vh, '-', 300px);
        @include u-hideElement;
        @include transforms(translateX(-50%), center);
        @include roundedAll();
        border: 1px solid $lightGray;
        overflow-y: auto;
        overflow-x: hidden;
        display: block;
        width: 250px;
        position: absolute;
        top: 80px;
        left: 50%;
        background: $white;
        padding: 10px;
        z-index: 2;
        ul li {
            a {
                @include roundedAll();
                @include transition();
                display: block;
                padding: 10px;
                color: $text;
                &:hover {
                    background: $mutedGray;
                }
            }
        }
    }
    .c-subMenu__mask {
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.10);
        z-index: 1;
        display: none;
    }
    
    // sub menu level 1 ativo
    &.is-active {
        z-index: 3;
        > .c-subMenu__call {
            &:after {
                @include transforms(rotate(180deg), center);
            }
        }
        > .c-subMenu__content {
            @include u-showElement;
            top: 40px;
        }
        .c-subMenu__mask {
            display: block;
        }
    }
    
    // sub level 2
    .c-subMenu {
        @include roundedAll();
        border: 1px solid transparent;
        margin-bottom: 0;
        > .c-subMenu__call {
            text-align: left;
            padding-right: 30px;
            font-weight: bold;
            &:after {
                right: 10px;
                left: inherit;
                bottom: 50%;
                @include transforms(translateY(50%), center);
            }
        }
        > .c-subMenu__content {
            @include transforms(translateX(0), center);
            width: 100%;
            left: 0;
            top: 0;
            background: $white2;
            overflow: hidden;
            position: relative;
            padding: 0 10px;
            max-height: 0;
            border: 0;
        }
        &.is-active {
            border-color: $lightGray;
            background: $mutedGray;
            margin-bottom: 5px;
            > .c-subMenu__call {
                &:after {
                    @include transforms(rotate(180deg), center);
                }
            }
            > .c-subMenu__content {
                overflow-y: auto;
                padding: 10px;
                max-height: 400px;
            }
        }
    }
    
    // sub with c-btn
    &.--with-c-btn {
        > .c-subMenu__call {
            max-width: 100%;
            text-align: left;
            padding-right: 50px;
            padding-left: 20px;
            &.c-btnWithIcon {
                display: flex;
            }
            &.--primary {
                &:after {
                    background: url(#{$media_assets}caret-down-white.svg);
                    background-repeat: no-repeat;
                    background-position: center;
                    background-size: contain;
                }
            }
            &:after {
                @include transforms(translateY(50%), center);
                left: inherit;
                right: 20px;
                bottom: 50%;
            }
        }
        &.is-active {
            > .c-subMenu__content {
                top: $btn_height;
            }
        }
    }
    
    // mobile
    &.--mobile {
        .c-subMenu__mask {
            display: none !important;
        }
        > .c-subMenu__call {
            text-align: left;
            padding-right: 30px;
            &:after {
                @include transforms(translateY(-50%), center);
                left: inherit;
                bottom: inherit;
                right: 0;
                top: 50%;
            }
        }
        > .c-subMenu__content {
            @include transforms(translateX(0), center);
            overflow-y: hidden;
            max-height: 0;
            position: relative;
            top: 0;
            left: 0;
            padding: 0 10px;
            display: block;
            width: 100%;
            border: 0;
            background: $white3;
        }
    
        &.is-active {
            > .c-subMenu__call {
                &:after {
                    @include transforms(translateY(50%) rotate(180deg), center);
                }
            }
            > .c-subMenu__content {
                overflow-y: auto;
                top: 0;
                padding: 10px;
                max-height: 2500px;
                margin: 10px 0 20px 0;
            }
        }
    
        .c-subMenu {
            .c-subMenu__call {
                padding-right: 35px;
                &:after {
                    right: 10px;
                }
            }
        }
    }
}
$c_subMenuHover_defaultHeight: 30px;
.c-subMenuHover {
    position: relative;
    width: 100%;
    > a {
        display: block;
        padding: 0 15px;
        height: $c_subMenuHover_defaultHeight;
        line-height: $c_subMenuHover_defaultHeight;
        border: 1px solid red;
        position: relative;
        &:after {
            content: '+';
            display: block;
            position: absolute;
            top: 0;
            right: 0;
            width: 10px;
            height: $c_subMenuHover_defaultHeight;
            line-height: $c_subMenuHover_defaultHeight;
            background: orange;
        }
        &.is-blocked-mobile {
            pointer-events: none;
        }
    }
    
    &__sub {
        @include transition();
        @include u-hideElement;
        display: block;
        width: 100%;
        position: absolute;
        background: $mutedGray;
    }
    
    &.--thisOpenInBottom {
        > .c-subMenuHover__sub {
            @include transforms(translateX(-50%), center);
            top: 40px;
            left: 50%;
            min-width: 150px;
            max-width: 450px;
        }
    }
    &.--thisOpenInRight {
        > .c-subMenuHover__sub {
            @include calc(left, 100%, '+', 10px);
            top: 0;
            min-width: 150px;
            max-width: 200px;
        }
    }
    &.--thisOpenInLeft {
        > .c-subMenuHover__sub {
            @include calc(right, 100%, '+', 10px);
            top: 0;
            min-width: 150px;
            max-width: 200px;
        }
    }
    
    &:hover {
        > .c-subMenuHover__sub {
            @include u-showElement;
        }
        &.--thisOpenInBottom {
            > .c-subMenuHover__sub {
                top: $c_subMenuHover_defaultHeight;
            }
        }
        &.--thisOpenInRight {
            > .c-subMenuHover__sub {
                left: 100%;
            }
        }
        &.--thisOpenInLeft {
            > .c-subMenuHover__sub {
                right: 100%;
            }
        }
    }
}
.c-whatsAppLink {
    position: fixed;
    bottom: 15px;
    right: 15px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    max-width: 60px;
    z-index: 12;
    &__icon {
        @include roundedAll(50%);
        @include flexWidth(60px);
        @include transition();
        background: $whatsappPrimary;
        background: radial-gradient(circle, $whatsappPrimary 0%, $whatsappSecondary 100%);
        -webkit-box-shadow: 0px 0px 29px -3px rgba(0,0,0,0.2);
        -moz-box-shadow: 0px 0px 29px -3px rgba(0,0,0,0.2);
        box-shadow: 0px 0px 29px -3px rgba(0,0,0,0.2);
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        z-index: 2;
        height: 60px;
        cursor: pointer;
        img, svg {
            @include transition();
            @include transforms(rotate(10deg));
            width: 30px;
            height: 28px;
            display: block;
            path {
                fill: $white !important;
            }
        }
        &:hover {
            @include transforms(scale(1.1), center);
        }
    }
    &__infos {
        @include u-hideElement;
        position: relative;
        z-index: 1;
        display: flex;
        flex-direction: row;
        align-items: center;
        flex-wrap: wrap;
        text-align: right;
        div {
            @include flexWidth(100%);
            display: block;
            width: 100%;
            max-height: 24px;
        }
        span {
            position: relative;
            right: -60px;
            display: inline-block;
            font-weight: 700;
            padding: 0 40px 0 15px;
            height: 24px;
            line-height: 24px;
            white-space: nowrap;
            overflow: hidden;
            &.wppCaption {
                @include transition();
                background: $mutedGray;
                color: $whatsappSecondary;
                font-size: 13px;
                @include letterSpacing(20);
                @include rounded(top, left, 15px);
                @include rounded(bottom, left, 15px);
                @include rounded(top, right, 15px);
            }
            &.wppNumber {
                @include transition($delay: 100ms);
                background: $white;
                color: $whatsappSecondary;
                font-size: 14px;
                @include letterSpacing(0);
                @include rounded(bottom, left, 15px);
                @include rounded(bottom, right, 15px);
            }
        }
    }
    &:hover {
        max-width: 480px;
        .m_whatsApp__infos {
            @include transition($delay: 100ms);
            @include u-showElement;
            span {
                right: -25px;
            }
        }
        .m_whatsApp__icon {
            img, svg {
                @include transforms(rotate(0deg));
            }
        }
    }
}

