    }
    &__header {
        @include transition();
        @include roundedAll();
        padding: 10px 20px;
        padding-right: 45px;
        display: block;
        position: relative;
        cursor: pointer;
        > * {
            @include transition();
        }
        &:after {
            @include transition();
            @include transforms(translateY(-50%), center);
            content: '';
            display: block;
            width: 14px;
            height: 14px;
            position: absolute;
            right: 20px;
            top: 50%;
            background: url(#{$media_assets}caret-down-white.svg);
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
