@mixin paragrafos(
    $tc: $text,
    $fw: $fwLight,
    $fs: 18px,
    $lh: 1.2,
    $mb: 20px,
    $mbLOT: 0,
    $important: false
) {
    color: $tc is-important($important);
    font-size: $fs is-important($important);
    font-weight: $fw is-important($important);
    margin-bottom: $mb;
    line-height: $lh is-important($important);
    &:last-of-type {
        margin-bottom: $mbLOT;
    }
    strong {
        font-weight: $fwBold;
        color: $tc;
        font-size: $fs;
        line-height: $lh;
    }
    a {
        color: $secondary;
        font-size: $fs;
        font-weight: $fw;
        line-height: $lh;
        text-decoration: none;
        &:hover {
            text-decoration: underline;
        }
    }
    @media screen and (max-width: 430px) {
        font-size: $fs - 2;
        a, strong {
            font-size: $fs - 2;
        }
    }
}

@mixin labels(
    $tc: $text,
    $fs: 16px,
    $important: false
) {
    color: $tc is-important($important);;
    font-size: $fs is-important($important);;
    line-height: 1.2 is-important($important);;
    font-weight: $fwSemiBold is-important($important);;
}

@mixin default__title() {
    display: block;
    font-weight: $fwLight;
    font-size: 30px;
    color: $darkGray;
    span {
        font-weight: $fwRegular;
        font-size: 30px;
        color: $primary;
    }
}

@mixin smallUppercase($tc: $white) {
    @include letterSpacing(10);
    display: block;
    font-weight: $fwSemiBold;
    font-size: 13px;
    color: $tc;
    text-transform: uppercase;
}

@mixin iconWithBrandBg($w: 42px, $h: 37px, $iw: 20px, $ih: 20px, $it: 0) {
    @include bg(assets, 'bg_icons.png');
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: $w;
    height: $h;
    i {
        display: block;
        width: $iw;
        height: $ih;
        position: relative;
        top: $it;
    }
}