// Layout Colors
$primaryLight: #ffc646;
$primary: #fdb514;
$primaryDark: #f2aa0a;

$secondaryLight: #111;
$secondary: #000;
$secondaryDark: #000;

$white: #fff;
$white2: #f9f9f9;
$white3: #f4f4f4;

$mutedGray: #ededed;
$lightGray: #e7e7e7;
$mediumGray: #c5c5c5;
$baseGray: #898989;
$darkGray: #4c4c4c;
$black: #000;

$defaultRadius: 5px;

$feedbackSuccess: #1ca33d;
$feedbackLoad: #eee;
$feedbackError: #f05555;

$whatsappPrimary: #25d366;
$whatsappSecondary: #1eb456;

// Sizes
$containerPadding: 0 15px;
$containerWidth: 1200px;
$containerBigWidth: 1540px;
$maxRowWidth: 2560px;

// media folders
$media_assets : "../../media/img/assets/";
$media_icons : "../../media/img/icons/";
$media_content : "../../media/img/content/";
$media_boilerplate : "../../media/img/boilerplate/";

// font
$text: #666;
$placeholderText: lighten($text, 15%);

@font-face {
  font-family: 'Source Sans Pro';
  src: url('https://cdn.jsdelivr.net/gh/gnlaures/neuro_sunset/media/type/SourceSansPro-Light.woff2') format('woff2'),
  url('https://cdn.jsdelivr.net/gh/gnlaures/neuro_sunset/media/type/SourceSansPro-Light.woff') format('woff');
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Source Sans Pro';
  src: url('https://cdn.jsdelivr.net/gh/gnlaures/neuro_sunset/media/type/SourceSansPro-Bold.woff2') format('woff2'),
  url('https://cdn.jsdelivr.net/gh/gnlaures/neuro_sunset/media/type/SourceSansPro-Bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Source Sans Pro';
  src: url('https://cdn.jsdelivr.net/gh/gnlaures/neuro_sunset/media/type/SourceSansPro-Regular.woff2') format('woff2'),
  url('https://cdn.jsdelivr.net/gh/gnlaures/neuro_sunset/media/type/SourceSansPro-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Source Sans Pro';
  src: url('https://cdn.jsdelivr.net/gh/gnlaures/neuro_sunset/media/type/SourceSansPro-Black.woff2') format('woff2'),
  url('https://cdn.jsdelivr.net/gh/gnlaures/neuro_sunset/media/type/SourceSansPro-Black.woff') format('woff');
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Source Sans Pro';
  src: url('https://cdn.jsdelivr.net/gh/gnlaures/neuro_sunset/media/type/SourceSansPro-Semibold.woff2') format('woff2'),
  url('https://cdn.jsdelivr.net/gh/gnlaures/neuro_sunset/media/type/SourceSansPro-Semibold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'League Gothic';
  src: url('https://cdn.jsdelivr.net/gh/gnlaures/neuro_sunset/media/type/LeagueGothic-Regular.woff2') format('woff2'),
  url('https://cdn.jsdelivr.net/gh/gnlaures/neuro_sunset/media/type/LeagueGothic-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
$fontFamily__sourceSans: 'Source Sans Pro', 'Arial', 'Calibri', sans-serif;
$fontFamily__leagueGothic: 'League Gothic', 'Source Sans Pro', 'Calibri', sans-serif;
$fontFamily__default: $fontFamily__sourceSans;

$fwLight: 300;
$fwRegular: normal;
$fwMedium: 600;
$fwSemiBold: 600;
$fwBold: bold;
$fwBlack: 900;